.App {
  text-align: center;
}

.covid-card {
  cursor: pointer;
  border: 2px solid transparent;
  border-radius: 5px;
  transition: border-color 0.2s;
}

.covid-card:hover {
  border-color: #CCCCCC;
}

.covid-card:focus {
  box-shadow: 0 0 0 2px #CCCCCC;
}

.covid-card-title {
  height: 60px;
  padding: 10px 10px 0;
  font-weight: 700;
  text-align: center;
}

.covid-card-content {
  display: flex;
  flex-direction: row;
  justify-items: center;
  flex-basis: 0;
  flex-grow: 1;
}

.covid-card-item {
  flex-basis: 0;
  flex-grow: 1;
}

.covid-card-number {
  text-align: center;
  white-space: nowrap;
  font-size: 1.5rem;
  line-height: 1.2;
  margin-bottom: 0;
}

.covid-card-text {
  text-align: center;
  opacity: .7;
  text-transform: uppercase;
  font-size: .8rem;
}

.covid-card--red {
  color: #E60000;
}

